import { accountLoginRequest, requestUserInfoById, updateUserInfoRequest } from '@/api/login'
import localCache from '@/utils/cache'
import { USERINFO_KEY } from '@/constants/cache'
import { getToken, setToken } from '@/utils/config'
import md5 from "md5"
import router from '@/router'
import { HOME_URL, LOGIN_URL } from '@/projectConfig'
import { Notification } from 'element-ui'
import { getTimeState } from '@/utils'
import { update } from 'lodash'

const loginModule = {
  namespaced: true,
  state() { // mapStateÂü∫Êú¨Áî®Ê≥ï ÔºåÂÄüÂä©vuexÁöÑmapStateÊñπÊ≥ïÔºåÂ∞Üstate‰∏≠ÁöÑÊï∞ÊçÆÊò†Â∞ÑÂà∞ÁªÑ‰ª∂‰∏≠Ôºå‰ª•‰æøÂú®ÁªÑ‰ª∂‰∏≠Ë∞ÉÁî® ---> ...mapState('loginModule', ['token', 'userInfo'])
    // ÈùûÁªÑ‰ª∂‰ΩøÁî®state‰∏≠ÁöÑÊï∞ÊçÆÔºåÈúÄË¶ÅÂÄüÂä©store.state.loginModule.token
    return {
      token: '',
      userInfo: {},
    }
  },
  getters: {},
  mutations: { // mapMutationsÂü∫Êú¨Áî®Ê≥ï ÔºåÂÄüÂä©vuexÁöÑmapMutationsÊñπÊ≥ïÔºåÂ∞Ümutations‰∏≠ÁöÑÊñπÊ≥ïÊò†Â∞ÑÂà∞ÁªÑ‰ª∂‰∏≠Ôºå‰ª•‰æøÂú®ÁªÑ‰ª∂‰∏≠Ë∞ÉÁî® ---> ...mapMutations('loginModule', ['changeToken'])
    // ÈùûÁªÑ‰ª∂‰ΩøÁî®mutations‰∏≠ÁöÑÊñπÊ≥ïÔºåÈúÄË¶ÅÂÄüÂä©store.commit('loginModule/changeToken', token)
    changeToken(state, token) {
      state.token = token
    },
    changeUserInfo(state, userInfo) {
      state.userInfo = userInfo
    },
    resetTokenAndUserInfo(state) {
      state.token = ''
      state.userInfo = {}
      localCache.clearCache()
    }
  },
  actions: { // mapActionsÂü∫Êú¨Áî®Ê≥ï ÔºåÂÄüÂä©vuexÁöÑmapActionsÊñπÊ≥ïÔºåÂ∞Üactions‰∏≠ÁöÑÊñπÊ≥ïÊò†Â∞ÑÂà∞ÁªÑ‰ª∂‰∏≠Ôºå‰ª•‰æøÂú®ÁªÑ‰ª∂‰∏≠Ë∞ÉÁî® ---> ...mapActions('loginModule', ['accountLoginAction'])
    // ÈùûÁªÑ‰ª∂‰ΩøÁî®actions‰∏≠ÁöÑÊñπÊ≥ïÔºåÈúÄË¶ÅÂÄüÂä©store.dispatch('loginModule/accountLoginAction', payload)
    async accountLoginAction({ commit }, payload) {
      // 1. ÁôªÂΩï
      const loginResult = await accountLoginRequest({ ...payload, password: md5(payload.password) })
      const { _id, token } = loginResult
      commit('changeToken', token)
      setToken(token)

      // 2. Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
      const userInfoResult = await requestUserInfoById(_id)
      commit('changeUserInfo', userInfoResult)
      localCache.setCache(USERINFO_KEY, userInfoResult)

      // Ë∑≥ËΩ¨Âà∞È¶ñÈ°µ
      router.push(HOME_URL)
      // ËøõÂÖ•È¶ñÈ°µËøôÈáåÈááÁî®ÈÄöÁü•Ôºå‰∏ç‰ΩøÁî®Ê∂àÊÅØÊèêÁ§∫„ÄêÊ≥®ÊÑè:ÂÖ≥Èó≠Êé•Âè£ÈáåÁöÑÊ∂àÊÅØÊèêÁ§∫„Äë
      Notification({
        title: getTimeState(),
        message: "Ê¨¢ËøéÁôªÂΩïÔºåÁÆÄ‰π¶ÂêéÂè∞ÁÆ°ÁêÜÁ≥ªÁªü",
        type: "success",
        duration: 3000,
      })
    },
    // vuexÂà∑Êñ∞Êï∞ÊçÆ‰∏¢Â§±ÈóÆÈ¢òËß£ÂÜ≥ÊñπÊ≥ï„ÄêÁõÆÂâç‰ΩøÁî®vuex-persistedstateÊèí‰ª∂„Äë
    // loadLocalLogin({ commit }) {
    //   const token = getToken()
    //   if (token) {
    //     const userInfo = localCache.getCache(USERINFO_KEY)
    //     userInfo && commit('changeUserInfo', userInfo)
    //   }
    // },
    logout({ commit }) {
      commit('resetTokenAndUserInfo')
      router.replace(LOGIN_URL)
    },
    async updateUserInfoAction({ commit }, payload) {
      const result = await updateUserInfoRequest(payload)
      commit('changeUserInfo', result)
      localCache.setCache(USERINFO_KEY, result)
    }
  }
}

export default loginModule