import axios from "axios"
import { BASE_URL, HttpState } from './config'
import { getToken } from '@/utils/config'
import store from "@/store"
import { LOGIN_URL } from '@/projectConfig'
import router from "@/router"
import { Message } from 'element-ui'

const instance = axios.create({
  baseURL: BASE_URL,
  timeout: HttpState.TIMEOUT
})

// ËØ∑Ê±ÇÊã¶Êà™
instance.interceptors.request.use(config => {
  // ËØ∑Ê±ÇÊã¶Êà™Ë¶ÅÂ§ÑÁêÜÁöÑÂÜÖÂÆπ
  getToken() && (config.headers
    .Authorization = `Bearer ${getToken()}`)
  return config
}, err => {
  console.error('ËØ∑Ê±ÇÂ§±Ë¥•', err)
})

// ÂìçÂ∫îÊã¶Êà™
instance.interceptors.response.use(res => {
  // ÂìçÂ∫îÊã¶Êà™Ë¶ÅÂ§ÑÁêÜÁöÑÂÜÖÂÆπ
  const { data } = res
  // ÈîôËØØÂ§ÑÁêÜ
  if (data.code === HttpState.OVERDUE) {
    // 401 tokenËøáÊúü
    store.dispatch('loginModule/resetTokenAndUserInfo')
    router.replace(LOGIN_URL)
    Message.error('ÁôªÂΩïÂ∑≤ËøáÊúüÔºåËØ∑ÈáçÊñ∞ÁôªÂΩï')
    return Promise.reject(data)
  }

  if (data.code !== HttpState.SUCCESS) {
    // ‰∏öÂä°ÈîôËØØ
    Message.error(data.msg)
    return Promise.reject(data)
  }

  // ÊàêÂäüÊ∂àÊÅØÊèêÁ§∫
  res.config.showMessage && Message.success(data.msg)

  // Ê≠£Â∏∏ËøîÂõûÊï∞ÊçÆ
  return data.data
}, err => {
  console.error('ÂìçÂ∫îÂ§±Ë¥•', err)
})

// Â∞ÅË£ÖhttpËØ∑Ê±ÇÁöÑÊñπÊ≥ï
/**
 *
 * @param {*} option
 * @param {*} option.method ËØ∑Ê±ÇÊñπÂºè
 * @param {*} option.path ËØ∑Ê±ÇË∑ØÂæÑ
 * @param {*} option.params ËØ∑Ê±ÇÂèÇÊï∞
 * @param {*} option.data ËØ∑Ê±ÇÊï∞ÊçÆ
 * @param {*} option.showSuccessMessage ÈªòËÆ§ÂºπÂá∫ÊàêÂäü | ÈîôËØØ | Ë≠¶Âëä Ê∂àÊÅØÊèêÁ§∫„Äê‰∏öÂä°Â±Ç->ÂêéÁ´ØÁªôÊàëËøîÂõûÁöÑcodeÔºåÈîôËØØ | Ë≠¶ÂëäÊèêÁ§∫‰∏ÄÂÆöÂºπÂá∫ÔºåÊàêÂäüÊ∂àÊÅØÊèêÁ§∫ÁïôÁªô‰ΩøÁî®ËÄÖËá™ÂÆö‰πâ„Äë
 * @returns
 */
async function http(option = {
  showSuccessMessage: true
}) {
  let result = null
  if (option.method === 'get' || option.method === 'delete') {
    try {
      result = await instance[option.method](option.path, { params: option.params })
    } catch (err) {
      result = err
    }
  } else if (option.method === 'post' || option.method === 'put') {
    try {
      result = await instance[option.method](option.path, option.data)
    } catch (err) {
      result = err
    }
  }
  return result
}

export default http

